import * as types from '../constants/ActionTypes';

const initialState = {
  portfolio: 'paintings test',

};

export default function portfolio(state = initialState, action) {
  switch (action.type) {

    case types.UPDATE_PORTFOLIO:
      return {
        ...state,
        portfolio: action.name,
      }

    default:
      return state;
  }
}


